import React, {Component} from "react";
import './jekyll-theme-cayman.scss';
import "./App.css";
import $ from 'jquery'

class FormsAndInputs extends Component {
  constructor(props){
    super(props)
    this.state = {
      titleVideo: null,
      textVideo: null
    }

  }

  // handleSubmit = (event) => {
  //   event.preventDefault()
  //   const data = this.state
  // }

  handleInputChange = (event) => {
    event.preventDefault()
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  // /* Api call to data that I may need */
  // componentDidMount(){
  //   this.setState({
  //     textVideo: null
  //   })
  // }

  // function1 = (data) => {
componentDidMount = () => {
    $("form").submit(function(event) {
        alert("You pressed the button")
        event.preventDefault();
        var titleVideo = $("#input-title").val();
        var textVideo = $("#input-text").val();
        var dropdown = $("#input-method").val();

        $.ajax({
            url: "http://localhost:8080/cgi-bin/Website.py",
            type: "POST",
            data: {
              titleVideo: titleVideo,
              textVideo: textVideo,
              dropdown: dropdown                  
            },
            // dataType: "text",
            succes: function(response){
              $("#form-message").html(response);
            }
            // succes: function(msg){
            //   alert("script ran ok");
            // },
          })
        });
  }

  render() {
     const {titleVideo} = this.state
     const {textVideo} = this.state

    return (

      <div className="main-content" id="main2">
        <h3>Insert the title and the text of a video below.</h3>
        <h4>Title:</h4>
        <form target="_blank">
          <p><input id="input-title" type="text" placeholder="Title" name="titleVideo" onChange={this.handleInputChange} /> {titleVideo} </p>     
          <h4>Text:</h4>
          <p><input id="input-text" type="text" placeholder="Text" name="textVideo" onChange={this.handleInputChange} />  {textVideo} </p>
          <h4>Classification algorithm:</h4>
         <select id="input-method" name = "dropdown">
          <option value = "Logistic Regression" selected>Logistic Regression</option>
          <option value = "Support Vector Machine">Support Vector Machine</option>
          <option value = "Recurrent Neural Network">Recurrent Neural Network</option>
         </select>
          <p></p>
          <p><input id="sub" type="submit" value="Submit"/></p>
          <p id="form-message"></p>
        </form>
      </div>

    )
  }
}


export default FormsAndInputs;